import { ChangeDetectionStrategy, Component, OnChanges } from '@angular/core';
import { Observable } from 'rxjs';

import { PresentationLoan } from '@backbase/data-ang/loans';
import { MediaQueryService } from '@backbase/ui-ang/media-query-lib';

import { LoanConfigBaseComponent } from '../../base/loan-config-base.component';
import {
  LoanSummaryFormulaData,
  LoanSummaryFormulaDataConfig,
  LoanSummaryFormulaViewType,
} from './loan-summary-formula.model';

@Component({
  selector: 'bb-loan-summary-formula',
  templateUrl: './loan-summary-formula.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanSummaryFormulaComponent<T extends PresentationLoan = PresentationLoan>
  extends LoanConfigBaseComponent<T, LoanSummaryFormulaDataConfig<T>>
  implements OnChanges
{
  /**
   * Loan formula view types
   */
  readonly viewTypes = LoanSummaryFormulaViewType;
  /**
   * Flag represents the tablet screen resolution
   */
  readonly isTabletResolution$: Observable<boolean> = this.mediaQueryService.isMediaBreakpointMatches(
    'max-width',
    'lg',
  );
  /**
   * Loan formula data
   */
  formulaData!: LoanSummaryFormulaData;

  constructor(private readonly mediaQueryService: MediaQueryService) {
    super();
  }

  /**
   * @internal
   */
  ngOnChanges(): void {
    this.formulaData = this.dataConfig.call(undefined, this.loan);
  }
}
