import { Directive, HostListener, Inject, Input, Renderer2 } from '@angular/core';
import { DOCUMENT } from '@angular/common';

export type LoanInfoLinkTargetType = '_blank' | '_parent' | '_self' | '_top';

// @dynamic
@Directive({
  selector: '[bbLoanInfoCustomLink]',
})
export class LoanInfoCustomLinkDirective {
  /**
   * Link url
   */
  @Input('bbLoanInfoCustomLink') linkUrl!: string;
  /**
   * Link download name
   */
  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('bbLoanInfoCustomLinkDownloadName') downloadName: string | undefined;
  /**
   * Link target
   *
   * @default '_self'
   */
  @Input('bbLoanInfoCustomLinkTarget') target: LoanInfoLinkTargetType = '_self';

  constructor(@Inject(DOCUMENT) private readonly document: Document, private readonly renderer: Renderer2) {}

  @HostListener('click')
  @HostListener('keydown.enter')
  @HostListener('keydown.space')
  simulateLinkClick(): void {
    const linkEl = this.renderer.createElement('a') as HTMLLinkElement;

    this.renderer.setAttribute(linkEl, 'href', this.linkUrl);
    this.renderer.setAttribute(linkEl, 'target', this.target);

    if (this.downloadName) {
      this.renderer.setAttribute(linkEl, 'download', this.downloadName);
    }

    this.renderer.appendChild(this.document.body, linkEl);
    linkEl.click();
    this.renderer.removeChild(this.document.body, linkEl);
  }
}
