import { ComponentFactoryResolver, Directive, Input, OnInit, Type, ViewContainerRef } from '@angular/core';

import { LoanItemCustomBase } from '../loan-info.model';
import { Loan } from '../../models/loan.model';

@Directive({
  selector: '[bbLoanInfoCustomSection]',
})
export class LoanInfoCustomSectionDirective<T extends Loan, U extends LoanItemCustomBase<T>> implements OnInit {
  /**
   * The class reference to the dynamic component which will be placed to the section
   */
  @Input() componentRef!: Type<U>;
  /**
   * Loan item
   */
  @Input() loan!: T;

  constructor(
    private readonly viewContainerRef: ViewContainerRef,
    private readonly componentFactoryResolver: ComponentFactoryResolver,
  ) {}

  ngOnInit(): void {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory<U>(this.componentRef);

    this.viewContainerRef.clear();

    const componentRef = this.viewContainerRef.createComponent(componentFactory);
    componentRef.instance.loan = this.loan;
  }
}
