import { ChangeDetectionStrategy, Component, OnChanges } from '@angular/core';

import { PresentationLoan } from '@backbase/data-ang/loans';

import { LoanPaymentInfoData, LoanPaymentInfoDataConfig, LoanPaymentInfoType } from './loan-item-payment-info.model';
import { LoanConfigBaseComponent } from '../../base/loan-config-base.component';

@Component({
  selector: 'bb-loan-item-payment-info',
  templateUrl: './loan-item-payment-info.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanItemPaymentInfoComponent<T extends PresentationLoan = PresentationLoan>
  extends LoanConfigBaseComponent<T, LoanPaymentInfoDataConfig<T>>
  implements OnChanges
{
  /**
   * Payment info data
   */
  paymentInfo!: LoanPaymentInfoData;
  /**
   * Flag represents fully paid loan
   */
  isFullyPaid!: boolean;
  /**
   * Flag represents visibility of the amount
   */
  isAmountVisible!: boolean;

  ngOnChanges(): void {
    this.paymentInfo = this.dataConfig.call(undefined, this.loan);

    this.isAmountVisible = Boolean(this.loan.currencyCode && this.paymentInfo.amount);
    this.isFullyPaid = this.paymentInfo.paymentType === LoanPaymentInfoType.FullyPaid;
  }
}
