import { ChangeDetectionStrategy, Component, EventEmitter, OnChanges, Output } from '@angular/core';

import { LoanListItemDataConfig } from './loan-list-item.model';
import { LoanConfigBaseComponent } from '../base/loan-config-base.component';
import { Loan, LoanDefaultViewType } from '../models/loan.model';
import { CustomLoanStatus } from './loan-item-status/loan-item-status.model';
import { getLoanAlias, isLoanOverdue } from '../utils/loan.util';

/**
 * @dynamic
 */
@Component({
  selector: 'bb-loan-list-item',
  templateUrl: './loan-list-item.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanListItemComponent<T extends Loan = Loan>
  extends LoanConfigBaseComponent<T, LoanListItemDataConfig<T>>
  implements OnChanges
{
  private static id = 0;
  /**
   * Loan select emitter
   */
  @Output() selectLoan = new EventEmitter<T>();

  /**
   * Container unique id
   */
  readonly itemId = LoanListItemComponent.id++;
  /**
   * Loan overdue status
   */
  readonly overdueStatus = CustomLoanStatus.Overdue;
  /**
   * Icon name by loan type
   */
  loanIconName!: string;
  /**
   * `Available balance` or `Amount repaid` amount based on loan type
   */
  amountByType: string | undefined;
  /**
   * Flag represents overdue state for loan
   */
  isOverdue!: boolean;
  /**
   * Loan alias
   */
  loanAlias!: string | undefined;

  /**
   * @internal
   */
  ngOnChanges(): void {
    const { viewType, availableBalance, paidAmount } = this.loan;

    this.loanIconName = this.dataConfig.icons[viewType];
    this.amountByType = viewType === LoanDefaultViewType.LineOfCredit ? availableBalance : paidAmount;
    this.isOverdue = isLoanOverdue(this.loan);
    this.loanAlias = getLoanAlias(this.loan);
  }
}
