import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import '@angular/localize/init';

import { LoansHttpService, PresentationDocumentMetadata } from '@backbase/data-ang/loans';

import { LoanItemCustomBase } from '../loan-info.model';
import { Loan } from '../../models/loan.model';

export interface LoanInfoDocument extends Required<Omit<PresentationDocumentMetadata, 'documentId' | 'contentType'>> {
  downloadUrl: string;
  previewUrl?: string;
}

@Component({
  selector: 'bb-loan-info-documents-section',
  templateUrl: './loan-info-documents-section.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanInfoDocumentsSectionComponent implements LoanItemCustomBase {
  private readonly defaultDocumentName = $localize`:Loan info document default name@@loans.loan-info.document.default-name:Document`;
  /**
   * Loan item
   */
  @Input() set loan(value: Loan) {
    this.updateLoanDocuments(value);
  }

  /**
   * Collection of filtered loan documents
   */
  loanDocuments: LoanInfoDocument[] | undefined;

  constructor(private readonly loansHttpService: LoansHttpService) {}

  /**
   * Document name tag id
   *
   * @param id
   */
  getDocumentNameId(id: number): string {
    return `bb-loan-info-document-${id}`;
  }

  /**
   * Updates loan documents collection
   *
   * @private
   */
  private updateLoanDocuments(loan: Loan): void {
    const loanId = loan.id;
    let id = 1;

    this.loanDocuments = loan.documents?.reduce<LoanInfoDocument[]>((accum, { documentId, contentType, name }) => {
      if (documentId) {
        const isPreviewAvailable = contentType === 'application/pdf';

        accum.push({
          name: name?.trim() || `${this.defaultDocumentName} ${id++}`,
          downloadUrl: this.getLoanDocumentUrl(loanId, documentId),
          previewUrl: isPreviewAvailable ? this.getLoanDocumentUrl(loanId, documentId, true) : undefined,
        });
      }

      return accum;
    }, []);
  }

  /**
   * Construct formatted url for preview or download
   *
   * @private
   */
  private getLoanDocumentUrl(loanId: string, documentId: string, inline?: boolean): string {
    return this.loansHttpService.downloadDocumentByLoanIdAndDocumentIdUrl({
      loanId,
      documentId,
      inline,
    });
  }
}
