import { ChangeDetectionStrategy, Component, Input, OnChanges, Type } from '@angular/core';

import {
  LoanInfoDataConfig,
  LoanInfoDataItem,
  LoanInfoItem,
  LoanInfoItemCustom,
  LoanInfoTitleBase,
  LoanItemCustomBase,
} from './loan-info.model';
import { Loan } from '../models/loan.model';
import { LoanConfigBaseComponent } from '../base/loan-config-base.component';

@Component({
  selector: 'bb-loan-info',
  templateUrl: './loan-info.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanInfoComponent<T extends Loan, U extends LoanItemCustomBase<T>>
  extends LoanConfigBaseComponent<T, LoanInfoDataConfig<T, U>>
  implements OnChanges
{
  /**
   * Whether currency local should be transformed to symbol
   */
  @Input() isCurrencySymbolUsed = false;
  /**
   * Filtered info sections
   */
  sections: LoanInfoDataConfig<T, U> = [];

  ngOnChanges(): void {
    this.filterSections();
  }

  /**
   * Typed reference to info item
   *
   * @param item
   */
  getTypedDataItem(item: LoanInfoItem<T, U>): LoanInfoDataItem<T> {
    return item as LoanInfoDataItem<T>;
  }

  /**
   * Typed reference to custom item component ref
   *
   * @param item
   */
  getItemComponentRef(item: LoanInfoItem<T, U>): Type<U> {
    return (item as LoanInfoItemCustom<T, U>).componentRef;
  }

  /**
   * Track by method used by *ngFor directive
   *
   * @param _index
   * @param title
   */
  trackByTitle<Y extends LoanInfoTitleBase>(_index: number, { title }: Y): string {
    return title;
  }

  /**
   * Filter sections by the items.
   * There are two options:
   * - check for the `showItem` function execution
   * - check the property value by `fieldKey`
   *
   * In case if all items in the section do not fit the condition, the sections will be omitted
   *
   * @private
   */
  private filterSections(): void {
    this.sections = this.dataConfig.reduce<LoanInfoDataConfig<T, U>>((accum, section) => {
      const items = section.items.filter((item) =>
        item.showItem
          ? item.showItem.call(undefined, this.loan)
          : Boolean(this.loan[(item as LoanInfoDataItem<T>).fieldKey]),
      );

      if (items.length) {
        accum.push({ ...section, items });
      }

      return accum;
    }, []);
  }
}
