import { ChangeDetectionStrategy, Component, EventEmitter, Input, OnChanges, Output } from '@angular/core';

import {
  drawAmountMenuItem,
  editAliasMenuItem,
  LoanSummaryDataConfig,
  LoanSummaryMenuItemKeys,
  payDownMenuItem,
} from './loan-summary-container.model';
import { LoanConfigBaseComponent } from '../base/loan-config-base.component';
import { getLoanAlias, isLoanOverdue } from '../utils/loan.util';
import { Loan } from '../models/loan.model';
import { InputInlineEditState } from '@backbase/ui-ang/input-inline-edit';
import { MediaQueryService } from '@backbase/ui-ang/media-query-lib';
import { LoanMenuItem } from '../loan-list-item/loan-item-menu/loan-item-menu.model';
import { Observable } from 'rxjs';

@Component({
  selector: 'bb-loan-summary-container',
  templateUrl: './loan-summary-container.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanSummaryContainerComponent<T extends Loan = Loan>
  extends LoanConfigBaseComponent<T, LoanSummaryDataConfig<T>>
  implements OnChanges
{
  private _aliasEditState!: InputInlineEditState;
  /**
   * Indicator to show/hide `payDown` button
   *
   * @default true
   */
  @Input() showPayDownButton = true;
  /**
   * Indicator to show/hide `payDown` button
   *
   * @default true
   */
  @Input() showDrawAmountButton = true;
  /**
   * Indicator to show/hide `edit alias` button
   *
   * @default true
   */
  @Input() showEditAliasButton = true;
  /**
   * Represents alias update state
   */
  @Input() set aliasEditState(value: InputInlineEditState) {
    this._aliasEditState = value;
  }

  get aliasEditState(): InputInlineEditState {
    return this._aliasEditState;
  }
  /**
   * Draw amount event emitter
   */
  @Output() drawAmount = new EventEmitter<void>();
  /**
   * Pay down event emitter
   */
  @Output() payDown = new EventEmitter<void>();

  /**
   * Emits on accepting new alias for save
   */
  @Output() updateAlias = new EventEmitter<string>();
  /**
   * Emits in canceling current alias update
   */
  @Output() cancelAliasUpdate = new EventEmitter<void>();
  /**
   * Flag represents IDLE state for alias editing
   */
  get isAliasEditIdleState(): boolean {
    return this._aliasEditState === InputInlineEditState.IDLE;
  }
  /**
   * Flag represents the screen resolution wider that `md` breakpoint
   */
  readonly isWiderThanMobile$: Observable<boolean> = this.mediaQueryService.isMediaBreakpointMatches('min-width', 'md');
  /**
   * Flag represents visibility of overdue banner
   */
  showOverdueBanner!: boolean;
  /**
   * Loan alias
   */
  loanAlias!: string | undefined;
  /**
   * Icon name by loan type
   */
  loanIconName!: string;
  /**
   * Context menu items
   */
  menuItems!: LoanMenuItem<LoanSummaryMenuItemKeys>[];

  constructor(private readonly mediaQueryService: MediaQueryService) {
    super();
  }
  /**
   * @internal
   */
  ngOnChanges(): void {
    this.showOverdueBanner = isLoanOverdue(this.loan);
    this.loanAlias = getLoanAlias(this.loan);
    this.menuItems = this.getMenuItems();
    this.loanIconName = this.dataConfig.icons[this.loan.viewType];
  }

  /**
   * Select an item for the context menu
   *
   * @param itemKey
   */
  onSelectMenuItem(itemKey: LoanSummaryMenuItemKeys) {
    switch (itemKey) {
      case 'editAlias':
        this._aliasEditState = InputInlineEditState.EDITING;
        break;
      case 'payDown':
        this.payDown.emit();
        break;
      case 'drawAmount':
        this.drawAmount.emit();
    }
  }

  private getMenuItems(): LoanMenuItem<LoanSummaryMenuItemKeys>[] {
    const menuItems: LoanMenuItem<LoanSummaryMenuItemKeys>[] = [];

    if (this.showPayDownButton) {
      menuItems.push(payDownMenuItem);
    }

    if (this.showDrawAmountButton) {
      menuItems.push(drawAmountMenuItem);
    }

    if (this.showEditAliasButton) {
      menuItems.push(editAliasMenuItem);
    }

    return menuItems;
  }
}
